import React, { useState, useEffect } from 'react';

const PlotlyWrapper = (props) => {
  const [Plot, setPlot] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    // Only load Plotly on client side
    if (typeof window !== 'undefined') {
      import('react-plotly.js')
        .then((module) => {
          setPlot(() => module.default);
          setLoading(false);
        })
        .catch((err) => {
          console.error('Failed to load react-plotly.js:', err);
          setError(err.message);
          setLoading(false);
        });
    }
  }, []);

  if (loading) {
    return (
      <div style={{
        width: '100%',
        height: '400px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        fontSize: '14px',
        color: '#666'
      }}>
        Loading chart...
      </div>
    );
  }

  if (error || !Plot) {
    return (
      <div style={{
        width: '100%',
        height: '400px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        fontSize: '14px',
        color: '#d32f2f',
        flexDirection: 'column',
        gap: '10px'
      }}>
        <div>Failed to load chart</div>
        {error && <div style={{ fontSize: '12px', opacity: 0.7 }}>{error}</div>}
      </div>
    );
  }

  return <Plot {...props} />;
};

export default PlotlyWrapper;